<template>
    <div>
        <div id="appdashboard" class="min-h-screen bg-gray-200 shadow">
            <div id="left-menu"
            :style="mobile?'':menuShort?'width:5vw;':'width:15vw;'" 
            :class="mobile?'ukuran-left-mobile bg-white shadow p-5':''"
            class="anim min-h-screen" v-if="showMenu"
            @mouseover="menuShort=false" @mouseleave="menuShort=true"
            >
            <span class="float-right pr-3 cursor-pointer font-semibold text-lg" v-if="mobile" @click="showMenu=false">x</span>
                <a @click="$router.push('/documentation')" class="mt-2">
                    <img src="https://www.pthis.id/public/img/hasnur/logo.png" class="p-2 ukuran-gambar-icon" >
                </a>
                <hr>
                <!-- <div v-for="(item, index) in groups" :key="index+'listMenu'"> -->
                    <!-- <div  class="py-2 pr-3 my-2 font-semibold text-sm shadow text-center"> -->
                        <!-- <span class="ml-2">{{item.charAt(0).toUpperCase()}}</span><span v-if="!menuShort">{{item.slice(1)}}</span>  -->
                    <!-- </div> -->
                    <p v-if="!menuShort" class="text-center">LIST DOC API</p> 
                    <div v-for="(item2, index2) in listMenu" :key="index2+'menus'" >
                        <a @click="$router.push('/documentation'+item2.link)" class="text-black">
                            <div :class="path[path.length-1]==item2.title?'bg-white':''" class="p-3 ml-2 hover:bg-white text-xs font-semibold cursor-pointer rounded-l-full"
                                @mouseover="hoverMenuCircle(item+index2)" @mouseleave="leaveMenuCircle(item+index2)"
                                style="position:relative;">
                                <!-- <div :id="'menuCircleA'+item+index2"
                                class="menu-circle-a bg-transparent rounded-circle d-none"></div>
                                <div :id="'menuCircleB'+item+index2"
                                class="menu-circle-b bg-transparent rounded-circle d-none"></div> -->
                                <img src="https://upload.egov.balangankab.go.id/svg/cog.svg" 
                                     class="pr-2 d-inline" style="height:30px;width:30px;" alt="">
                                <span v-if="!menuShort">{{item2.title}}</span>
                               
                        </div>
                       </a>
                    </div>
                <!-- </div> -->
                <br>
                <br>
            </div>
            <div 
            id="right-menu-pertama"
            :style="mobile?'width:100vw;':menuShort?'width:95vw;left:5vw;':'width:85vw;left:15vw;'"
            class="h-16 bg-white shadow rounded d-flex d-flex justify-content-between align-items-center anim">
                <div class="align-items-start ml-4">
                    <button type="button" class="btn btn-sm btn-dark" @click="!mobile?menuShort=!menuShort:showMenu=!showMenu">
                        <img src="https://upload.egov.balangankab.go.id/svg/th-list.svg" 
                             class="d-inline" style="height:20px;width:20px;filter: brightness(0) invert(1);" alt="">
                    </button>
                </div>
                <div class="align-items-start">
                    <div class="sm-form ">
                        <input type="text" style="width:30vw;" id="Search" name="Search" 
                        class="form-control form-control-sm p-3 shadow rounded-full" placeholder="Search" >
                    </div>
                </div>
                <div></div>
                <div class="align-items-start mr-10 position-relative">
                   <img style="height:40px;" @click='profileMenu=!profileMenu' class="rounded-circle cursor-pointer" src="https://www.pthis.id/public/img/hasnur/logo.png" alt="">
                </div>
            </div>
            <div v-if="profileMenu" 
            style="position:absolute;z-index:10000;right:10px;top:10px;min-width:250px;"
             class="shadow p-3 rounded bg-white">
                <p class="float-right p-2 text-xs font-semibold">
                    USER
                </p>
                <img @click="profileMenu=!profileMenu" style="height:40px;" class="rounded-circle" src="https://www.pthis.id/public/img/hasnur/logo.png" alt="">
                <hr>
                <p class="p-2 text-sm hover:bg-blue-300 cursor-pointer my-1 rounded ">
                    <a @click="$router.push('/')">Go Back</a>
                </p>
            </div>
            <div v-if="profileMenu" @click="profileMenu=false" style="width:100vw;height:100vh;position:fixed;z-index:101;top:0;left:0;"></div>
        </div>
        <div 
        id="right-menu-kedua"
        class=" bg-white shadow p-3 anim" >
            <main>
                <router-view></router-view>
            </main>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default{
    data(){
        return{
            menuShort:true,
            showMenu:false,
            mobile:false,
            profileMenu:false,
            groups:[],
            listMenu:[],
            path:'http://localhost',
            test:"welcome"
        }
    },
    methods: {
        getMenu(){
            axios.get('/menu.json').then(res=>{
                this.listMenu=res.data;
                this.$forceUpdate();
            });
            axios.get('/group.json').then(res=>{
                this.groups=res.data;
                this.$forceUpdate();
            });
        },
        hoverMenuCircle(index){
            document.querySelector('#menuCircleA'+index).classList.remove('d-none');
            document.querySelector('#menuCircleB'+index).classList.remove('d-none');
        },
        leaveMenuCircle(index){
            document.querySelector('#menuCircleA'+index).classList.add('d-none');
            document.querySelector('#menuCircleB'+index).classList.add('d-none');
        },
        watchMenuKanan(){
            if(this.mobile){
                document.getElementById('right-menu-kedua').style.width='94vw';
            }else{
                if(this.menuShort){
                    document.getElementById('right-menu-kedua').style.width='94vw';
                    document.getElementById('right-menu-kedua').style.left='5vw';
                }else{
                    document.getElementById('right-menu-kedua').style.width='85vw';
                    document.getElementById('right-menu-kedua').style.left='15vw';
                }
            }
        }
    },
    watch:{
        menuShort(){
            this.watchMenuKanan();
        }
    },
    mounted() {
        let that=this;
        function reportWindowSize() {
            if(window.innerWidth<600){
                that.mobile=true;
                document.getElementById('right-menu-kedua').style.width='100vw';
                that.showMenu=false;
            }else{
                that.mobile=false;
                that.showMenu=true;
            }
        }
        reportWindowSize();
        this.watchMenuKanan();
        window.onresize = reportWindowSize;
        this.getMenu();
    }
}
</script>
<style>
.ukuran-left-mobile{
    width:90vw;
    margin:10px;
    height:100vh;
    position:fixed;
    z-index:1000;
}
.ukuran-gambar-icon{
    height:70px;
    display:block;
    margin:auto;
}
.menu-circle-a {
    content:'';
    width:50px;
    height:50px;
    border-radius: 50%;
    box-shadow:35px 35px 0 10px var(--white);
    position:absolute;
    top:-50px;
    right:0;
    z-index:1000;
}
.menu-circle-b{
    content:'';
    width:50px;
    height:50px;
    border-radius: 50%;
    box-shadow:35px -35px 0 10px var(--white);
    position:absolute;
    bottom:-50px;
    right:0;
    z-index:1000;
}
#right-menu-pertama{
    position:fixed;
    z-index:100;
    top:0;
    width:100vw;
}
#right-menu-kedua{
    position:fixed;
    z-index:100;
    top:70px;
    width:100vw;
    overflow:scroll;
    height:90vh;
}
#profileMenu{
    position:fixed;
    top:10px;
    right:10px;
    width:200px;
    min-height:400px;
    z-index:102;
}
</style>